$brd-10 : 10px;
$brd-30 : 30px;
$p10: 10px;
$zero: 0 !important;
$white: white;
$cb: black;
$blk0: #ffffff00;
$blk40: rgba(26, 47, 62, 0.486);
$p100: 100%;
$flex: flex;
$row-r: row-reverse;
$brd1: 1px;
$sld: solid;
$mc: #022374;
$cd: #00132f;
$cs: #fab52c;
$rm07: 0.7rem;

/**Edicion bloque primary menu*****/
header {
    box-shadow: 0px 2px 11px 0px #f1f1f1;
    margin-bottom: 1%;
}

.custom-flex{
    height: 5vh;
}
.custom-flex div {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: $p100;
}
 
/**edicion search*/
.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
}

.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
    border: $brd1 $sld $mc;
    border-radius: $rm07;
    height: $p100;
    font-size: 0.7rem;
}
.busqueda input#edit-keys--2, .busqueda input#edit-keys {
    border: none;
    background: $blk0;
    border-left-width: $brd1;
    border-left-color: $cb;
    border-left-style: $sld;
    padding-left: $p10;
}
.busqueda input#edit-keys--2:focus-visible, .busqueda input#edit-keys:focus-visible {
    outline: none;
}
.busqueda input#edit-submit, .busqueda input#edit-submit--3 {
    background: $blk0;
    border: none;
}
/**edicion botones primary nav*/
.primary-nav-boton a.btn {
    background-color: #022374;
    min-width: 150px;
    margin-left: 2px;
    border: none;
    color: #00132f;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.7rem;
    font-size: 0.7rem;
}
.primary-nav-boton a.btn-main-color.btn-color.btn i {
    margin-left: 4px;
}
.primary-nav-boton a.btn-main-color.btn-color.btn{
    background-color: $mc;
    color: $white;
}
.primary-nav-boton a.btn-secundary.btn{
    background-color: $cs;
}


/**edicion area de titulo de cada pagina**/
.back-title{
    background-image: url('../images/computer-g4b10d37c4_1920.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y:center;
    height: 30vh;
    border-radius: $brd-30 ;
    color: $white;
    padding: $zero;
    margin: $zero;
}
.blur{
    background-color: $blk40;
    width: $p100;
    height: $p100;
    border-radius: $brd-30;
}
.idiomas ul.links {
    background: black;
}

/**site logo al 100% **/
a.navbar-brand {
    width: $p100;
}
a.navbar-brand img {
    width: $p100;
}
/*secundary nav*/

header .custom-heigth {
    height: 10vh;
    font-size: 1.4rem;
    font-weight: 300;
}
header .custom-heigth div {
    height: 100%;
    width: 100%;
}
header ul.navbar-nav.ml-auto {
    height: 100%;
    align-items: flex-end;
    padding-bottom: 2%;
    width: 100%;
    justify-content: flex-end;
}
header li.nav-item {
    height: 2vh;
}
header a.nav-item.nav-link {
    padding-top: 0;
    padding-bottom: 0;
    height: $p100;
    line-height: 2vh;
    color:$cd !important;
}
header li.nav-item:first-child {
    border-right: 2px solid $cd;
}
header li.nav-item:last-child {
    border: none;
}
header a.nav-item.nav-link.is-active {
    font-weight: 600;
}
header li.nav-item {
    height: 2vh;
    border-right: 2px solid $cd;
}
@media (min-width:1366px){
    header li.nav-item {
        padding: 0 4px;
    }
}
/***personalizacion de slider*/
.flexslider{
    margin: 0;
}

.main-banner .flex-direction-nav a:before {
    font-size: 30px;
}
.main-banner .flex-direction-nav a {
    background: #ffffff;
    border-radius: 40px;
}
.main-banner .flex-direction-nav .flex-next {
    right: 0;
    text-align: center;
}
.main-banner .flex-direction-nav .flex-prev{
    left: 0;
    text-align: center;
}
.main-banner .flexslider:hover .flex-direction-nav .flex-prev {
    left: 80%;
    top: 80%;
}
.main-banner .flexslider:hover .flex-direction-nav .flex-next {
    right: 10%;
    top: 80%;
}
.main-banner .field-content {
    padding: 0 3rem 3rem 3rem;
}
.main-banner .views-field.views-field-body {
    position: absolute;
    width: 50vw;
    bottom: 4%;
    padding: 0 20px;
}

.custom-slider-container {
    color: white;
    text-shadow: 1px 1px #626262;
}
a.boton-banner-1 {
    color: white;
    text-shadow: 1px 1px #898989;
    padding: 1.5% 4%;
    background-color: #c0cbd7;
    border-radius: 30px;
    border: 1px solid white;
    text-decoration: none;
}

a.boton-banner-1:hover, a.boton-banner-1:focus {
    text-decoration: none;
    color: white;
}
.custom-slider-container hr {
    width: 50% !important;
    margin: inherit !important;
    border-top: 1px solid white !important;
}

/***footer*/
footer {
    background-color: #0739aa;
    border-radius: 30px 30px 0 0;
    box-shadow: 1px -6px 10px 2px #e9e9e999;
    margin-top: 4%;
}
    
.custom-footer-bk-white {
    background: white;
}

.custom-footer-primary {
    border-radius: 29px 29px 0 0;
}
.custom-footer-third {
    border-radius: 0 0 29px 29px;
}
/***edicion card colombia*/

.imagen-foot .views-row {
    display: flex;
    align-content: center;
    align-items: stretch;
}
.imagen-foot .views-field.views-field {
    display: flex;
    align-items: center;
}
.imagen-foot .field-content {
    width: 100%;
}
.imagen-foot .field-content img {
    width: 100%;
    height: auto;
}
.imagen-foot .views-field.views-field-field-img-foot {
    padding: 10px;
    width: 20%;
    background-color: #e1ebfc;
    border-radius: 10px 0 0 10px;
}
.imagen-foot .field-content p {
    margin: 0;
}
.imagen-foot .views-field.views-field-field-basico {
    width: 40%;
    background-color: #0066d0;
    padding-left: 10px;
    color: white;
    font-weight: bold;
}
.imagen-foot .views-field.views-field-field-texto-segundo {
    width: 40%;
    background-color: #439aff;
    padding-left: 10px;
    border-radius: 0 10px 10px 0;
    color: white;
    font-weight: 300;
}
/***imagenes footer*/
.imagenes-footer div {
    display: flex;
    align-items: center;
    padding: 6%;
    width: 100%;
    height: 100%;
    justify-content: flex-start;
}
.imagenes-footer img:nth-child(1) {
    width: 7%;
}
.imagenes-footer img:nth-child(2) {
    width: 15%;
    margin-left: 50px;
}

/*iconos redes sociales*/
.custom-rs .redondo {
    width: 2rem;
    height: 2rem;
    background: #439aff;
    text-align: center;
    border-radius: 2rem;
    line-height: 2rem;
    color: white;
}
.custom-rs .redondo:hover {
    background: #a5cfff;
}
.custom-rs .enlace{
    color:$cb;
}
.custom-rs .enlace:hover{
    color: #959595;
    text-decoration: none;
}
.custom-rs div {
    width: 100%;
}
.custom-rs .field-content {
    display: flex;
    justify-content: space-around;
}

/*enlaces footer 3*/
.custom-footer-third {
    height: 9vh;
}
.custom-footer-third div {
    width: 100%;
}
.enlaces-footer-genral div {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
}
.enlaces-footer-genral a {
    color: lightgrey;
}
.enlaces-footer-genral a:hover {
    color: #818181;
    text-decoration: none;
}

/*CONTADOR*/

.count .pantalla {
    width: 100%;
    height: 100%;
    background: #ffffff00;
    position: absolute;
}
.position-relative.count {
    width: 10%;
}


